import React, { Component } from "react";
import devopsCertificate from "../media/infosys-logo.png";
import redhatCertificate from "../media/redhat-logo.png";
import instaAward from "../media/insta-award.png";

class Awards extends Component {
  render() {
    return (
      <div>
        <div className="education-top-bottom">
          <h1 align="center">
            <strong>Awards</strong>
          </h1>
        </div>
        <div className="mdl-grid grid">
          <div className="mdl-cell--12-col mdl-shadow--4dp grid-large-box"></div>
          <div className="mdl-cell--12-col mdl-shadow--4dp grid-large-box">
            <div className="mdl-grid">
              <div className="mdl-cell--6-col">
                <center>
                  <img src={instaAward} className="img-responsive" alt="SKIT" />
                </center>
              </div>
              <div className="mdl-cell--6-col grid-large-box-details">
                <h3>Infosys Insta Award</h3>

                <h6>
                  <strong>Earned On: 27 August, 2019</strong>
                </h6>
                <br />
                <h6>
                  Earned this certificate of appreciation from VP of Infosys for
                  doing good work in projects.
                </h6>
                <br />
                <br />
                <br />
                <center>
                  <a
                    className="mdl-button mdl-js-button mdl-button--raised mdl-button--colored mdl-js-ripple-effect"
                    href="https://drive.google.com/open?id=19qz-xrtaHxKOrIjkVga5UtBEBr8rB5eA"
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ textDecoration: "none" }}
                  >
                    <strong>Certificate Link</strong>
                  </a>
                </center>
              </div>
            </div>
          </div>
          <div className="mdl-cell--12-col mdl-shadow--4dp grid-large-box">
            <div className="mdl-grid">
              <div className="mdl-cell--6-col">
                <center>
                  <img
                    src={redhatCertificate}
                    className="img-responsive"
                    alt="NPSSS"
                  />
                </center>
              </div>
              <div className="mdl-cell--6-col grid-large-box-details">
                <h3>Red Hat Accredited Professional</h3>

                <h6>
                  <strong>Earned On: 01 February, 2019</strong>
                </h6>
                <br />
                <h6>
                  Earned this certificate for successfully completing{" "}
                  <strong>
                    Red Hat Delivery Specialist - Container Platform Deployment
                  </strong>
                  &nbsp; curriculam.
                </h6>
                <br />
                <br />
                <br />
                <center>
                  <a
                    className="mdl-button mdl-js-button mdl-button--raised mdl-button--colored mdl-js-ripple-effect"
                    href="https://drive.google.com/open?id=1wD2QCjt-a6_nHZVozAPozJ1dLfcT_SPZ"
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ textDecoration: "none" }}
                  >
                    <strong>Certificate Link</strong>
                  </a>
                </center>
              </div>
            </div>
          </div>
          <div className="mdl-cell--12-col mdl-shadow--4dp grid-large-box">
            <div className="mdl-grid">
              <div className="mdl-cell--6-col">
                <center>
                  <img
                    src={devopsCertificate}
                    className="img-responsive"
                    alt="SKIT"
                  />
                </center>
              </div>
              <div className="mdl-cell--6-col grid-large-box-details">
                <h3>Infosys Certified DevOps Professional</h3>

                <h6>
                  <strong>Earned On: 04 December, 2018</strong>
                </h6>
                <br />
                <h6>
                  Earned this certificate for successfully completing{" "}
                  <strong>Infosys DevOps Professional</strong>
                  &nbsp;assessment.
                </h6>
                <br />
                <br />
                <br />
                <center>
                  <a
                    className="mdl-button mdl-js-button mdl-button--raised mdl-button--colored mdl-js-ripple-effect"
                    href="https://drive.google.com/open?id=1FQzA69ibK-9OhCz0SkNl7OGLPTEZ-Wfl"
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ textDecoration: "none" }}
                  >
                    <strong>Certificate Link</strong>
                  </a>
                </center>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Awards;
