import React, { Component } from "react";
import infosysLogo from "../media/infosys-logo.png";
import isroLogo from "../media/isro-logo.png";

class WorkExperience extends Component {
  render() {
    return (
      <div>
        <div className="education-top-bottom">
          <h1 align="center">
            <strong>Work Experience</strong>
          </h1>
        </div>
        <div className="mdl-grid grid">
          <div className="mdl-cell--12-col mdl-shadow--4dp grid-large-box"></div>
          <div className="mdl-cell--12-col mdl-shadow--4dp grid-large-box">
            <div className="mdl-grid">
              <div className="mdl-cell--6-col">
                <img
                  src={infosysLogo}
                  className="img-responsive"
                  alt="CovalentCapital"
                />
              </div>
              <div className="mdl-cell--6-col grid-large-box-details">
                <h2>Systems Engineer - Infosys Limited</h2>
                <br />
                <h5>
                  <strong>May 2018 - Present</strong>
                </h5>
                <br />
                <ul>
                  <li>
                    Working with Center Of Excellence(COE) team of Infosys.
                  </li>
                  <li>Working on DevOps related assignments.</li>
                  <li>
                    Working on CI-CD implementations and complete automation for
                    DevOps workflows.
                  </li>
                </ul>
                <br />
                <center>
                  <a
                    className="mdl-button mdl-js-button mdl-button--raised mdl-button--colored mdl-js-ripple-effect"
                    href="https://www.infosys.com/"
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ textDecoration: "none" }}
                  >
                    <strong>Company Website</strong>
                  </a>
                </center>
              </div>
            </div>
          </div>
          <div className="mdl-cell--12-col mdl-shadow--4dp grid-large-box">
            <div className="mdl-grid">
              <div className="mdl-cell--6-col">
                <img
                  src={isroLogo}
                  className="img-responsive"
                  alt="CovalentCapital"
                />
              </div>
              <div className="mdl-cell--6-col grid-large-box-details">
                <h2>Intern - Indian Space Research Organisation(ISRO)</h2>
                <br />
                <h5>
                  <strong>June 2017 - July 2017(60 Days)</strong>
                </h5>
                <br />
                <ul>
                  <li>Developed a Virtual Reality based Mobile Application.</li>
                  <li>
                    Tools &amp; Technology Used: Unity, Google Cardboard and
                    Android SDK
                  </li>
                </ul>
                <br />
                <center>
                  <a
                    className="mdl-button mdl-js-button mdl-button--raised mdl-button--colored mdl-js-ripple-effect"
                    href="https://www.isro.gov.in/"
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ textDecoration: "none" }}
                  >
                    <strong>Company Website</strong>
                  </a>
                </center>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default WorkExperience;
