import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLinkedin } from "@fortawesome/free-brands-svg-icons";
import { faFacebook } from "@fortawesome/free-brands-svg-icons";
import { faTwitter } from "@fortawesome/free-brands-svg-icons";
import { faInstagram } from "@fortawesome/free-brands-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";

class Footer extends Component {
  iconDecoration = {
    textDecoration: "none",
    color: "inherit"
  };
  render() {
    return (
      <footer className="mdl-mini-footer">
        <span className="symbol">
          <a
            className="plinks facebook"
            href="https://www.facebook.com/sourabh.gupta.942"
            rel="noopener noreferrer"
            target="_blank"
          >
            <FontAwesomeIcon icon={faFacebook} size="1x" />
          </a>
        </span>
        <span className="symbol">
          <a
            className="plinks twitter"
            href="https://twitter.com/sourabhgupta385"
            rel="noopener noreferrer"
            target="_blank"
          >
            <FontAwesomeIcon icon={faTwitter} size="1x" />
          </a>
        </span>
        <span className="symbol">
          <a
            className="plinks instagram"
            href="https://www.instagram.com/sourabh._gupta/"
            rel="noopener noreferrer"
            target="_blank"
          >
            <FontAwesomeIcon icon={faInstagram} size="1x" />
          </a>
        </span>
        <span className="symbol">
          <a
            className="plinks linkedin"
            href="https://www.linkedin.com/in/sourabh-gupta-0a7a0510b"
            rel="noopener noreferrer"
            target="_blank"
          >
            <FontAwesomeIcon icon={faLinkedin} size="1x" />
          </a>
        </span>
        <span className="symbol">
          <a
            className="plinks github"
            href="https://github.com/sourabhgupta385"
            rel="noopener noreferrer"
            target="_blank"
          >
            <FontAwesomeIcon icon={faGithub} size="1x" />
          </a>
        </span>

        <br />
        <br />
        <h3>
          <span id="copyright-year"> 2020</span>
          &nbsp;Sourabh Gupta
        </h3>
      </footer>
    );
  }
}

export default Footer;
