import React, { Component } from "react";
import { HashRouter as Router, Route } from "react-router-dom";
import NavBar from "./components/navbar";
import SideMenu from "./components/sideMenu";
import Footer from "./components/footer";
import Overview from "./components/overview";
import Skills from "./components/skills";
import WorkExperience from "./components/workExperience";
import Awards from "./components/awards";
import Education from "./components/education";
import Projects from "./components/projects";
import Contact from "./components/contact";
import withTracker from "./withTracker";

class App extends Component {
  componentDidMount() {
    document.title = "Sourabh Gupta - Systems Engineer at Infosys Ltd.";
  }
  render() {
    return (
      <Router>
        <div className="mdl-layout mdl-js-layout mdl-layout--fixed-drawer mdl-layout--fixed-header">
          <NavBar />
          <div className="mdl-layout__drawer">
            <SideMenu />
          </div>
          <main className="mdl-layout__content">
            <div className="page-content">
              <Route path="/skills" component={withTracker(Skills)} />
              <Route
                path="/work-experience"
                component={withTracker(WorkExperience)}
              />
              <Route path="/projects" component={withTracker(Projects)} />
              <Route path="/awards" component={withTracker(Awards)} />
              <Route path="/education" component={withTracker(Education)} />
              <Route path="/contact" component={withTracker(Contact)} />
              <Route path="/overview" component={withTracker(Overview)} />
              <Route exact path="/" component={withTracker(Overview)} />
              <div align="center">
                <Footer />
              </div>
            </div>
          </main>
        </div>
      </Router>
    );
  }
}

export default App;
