import React, { Component } from "react";
import { Link } from "react-router-dom";

class SideMenu extends Component {
  render() {
    return (
      <nav className="mdl-navigation">
        <Link className="mdl-navigation__link sidemenu" to="/overview">
          Overview
        </Link>
        <Link className="mdl-navigation__link sidemenu" to="/skills">
          Skills
        </Link>
        <Link className="mdl-navigation__link sidemenu" to="/work-experience">
          Work Experience
        </Link>
        <Link className="mdl-navigation__link sidemenu" to="/projects">
          Projects
        </Link>
        <a
          className="mdl-navigation__link sidemenu"
          href="https://blogs.sourabhgupta.in"
          target="_blank"
          rel="noopener noreferrer"
        >
          DevOps Blogs
        </a>
        <Link className="mdl-navigation__link sidemenu" to="/awards">
          Awards
        </Link>
        <Link className="mdl-navigation__link sidemenu" to="/education">
          Education
        </Link>
        <a
          className="mdl-navigation__link sidemenu"
          href="https://medium.com/@sourabh_gupta"
          target="_blank"
          rel="noopener noreferrer"
        >
          Blogs on Medium
        </a>
        <a
          className="mdl-navigation__link sidemenu"
          href="https://drive.google.com/open?id=1ch3EwYF798WP5l5atE1XHZQz2cDryu5v"
          target="_blank"
          rel="noopener noreferrer"
        >
          Resume
        </a>

        <a
          className="mdl-navigation__link sidemenu"
          href="https://github.com/sourabhgupta385"
          target="_blank"
          rel="noopener noreferrer"
        >
          Code on Github
        </a>
        <Link className="mdl-navigation__link sidemenu" to="/contact">
          Contact
        </Link>
      </nav>
    );
  }
}

export default SideMenu;
