import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBriefcase } from "@fortawesome/free-solid-svg-icons";
import { faMapMarkerAlt } from "@fortawesome/free-solid-svg-icons";
import sourabh from "../media/sourabh.jpg";
import "../css/styles.css";

class Overview extends Component {
  render() {
    return (
      <div className="view" align="center">
        <img src={sourabh} className="mainimage" alt="profile" />
        <br />
        <br />
        <h2>Hi, I am Sourabh Gupta</h2>
        <h3>
          <FontAwesomeIcon icon={faMapMarkerAlt} size="1x" /> &nbsp;
          Ramganjmandi, Rajasthan (India)
        </h3>

        <div className="quote">
          <h4>
            <FontAwesomeIcon icon={faBriefcase} size="1x" /> &nbsp; Systems
            Engineer at Infosys Limited
          </h4>
        </div>
        <br />
        <p>
          Sourabh is a versatile DevOps engineer with 1.9+ years of experience.
          His work for DevOps implementation in one of the project earned him
          Infosys Insta Award. He likes to work on CI/CD implementation and also
          has side passion for React projects. When he isn't glued to computer
          screen then he spends most of his time in exploring new places.
        </p>
      </div>
    );
  }
}

export default Overview;
